	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 6 - The encounter","texte":{"childMarkdownRemark":{"html":"<p>I walk calmly along the side of the road towards the town that had been indicated to me. Cars drive by me like nothing happened. A car finally stops. A woman drives the car. She looks me in the eye, I look her in the eye too. She smiles for a brief moment and lowers her car window.</p>\n<p>Woman: Need to go somewhere?<br>\nWamien: Depends where you go, I go to Dais.</p>\n<p>Telling me I could use the young woman to get there faster.</p>\n<p>Woman: I go this way, I can take you there if you want.</p>\n<p>She press the button that unlocks the passenger door.</p>\n<p>Wamien : Okay, thank you.</p>\n<p>I open the car door, sit down and tie myself up.</p>\n<p>Woman: What is your name, sir? </p>\n<p>She says that to me by turning her head towards me, giving a small blow with her beautiful and long blond hair.</p>\n<p>Wamien: Wamien, and you?<br>\nClara: I'm Clara. Clara Nairo.<br>\nWamien : Nice to meet you, Mrs Clara Nairo.<br>\nClara: Me too, Mr. Wamien.</p>\n<p>The road to the city was not very long. We went downtown.</p>\n<p>Clara: Um, Wamien?</p>\n<p>She's blushing a little.</p>\n<p>Wamien : Yes?<br>\nClara: I can leave you my contact details if you ever want to come back to town.<br>\nWamien: Okay.</p>\n<p>She smiled, took a piece of paper, wrote down her contact information. I take the paper, put it in my pants pocket and get out of the car. I turn around and lean a little towards her.</p>\n<p>Wamien : Thanks for the lift.<br>\nClara: Always a pleasure to help others.</p>\n<p>She answered with a smile before leaving to do her things. I look around wondering where I should be going. I discreetly plug the USB stick into a small place on my wrist. The GPS shows me the direction to take to get into the building. A block apparently was facing me. It is therefore easy for me to enter as if nothing had happened. I enter and head towards the block apparently Mickaël. I'm putting my eyes in heat detector mode. There was someone in the apartment, a man, sure it was Michael. I knock gently on the door. I see the man's reaction, a start. He gets up and walks to the door. He opens it a little.</p>\n<p>Mickaël: Yes?<br>\nWamien : Hello, Mr Stephan?<br>\nMickaël : Yes, can I help you?</p>\n<p>He seemed a little disturbed, as if we were cutting him off in what he was doing.</p>\n<p>Wamien : I would like to know if you would like to go on a trip to the kingdom of Mahinu. I won a coupon to go, but I don't need it.<br>\nMickaël : I don't know you and no thanks, I already lived there.</p>\n<p>I had time to slide my foot to prevent the door from closing.</p>\n<p>Wamien : Well, more seriously, I know things about you, Mr. Stephan. You better cooperate if you want a fair trial.<br>\nMickaël: Get the hell out of my house!</p>\n<p>The man tries to punch me in the face that I duck without difficulty. I take out a pair of handcuffs hidden at the level of my belt and attach a wrist to it. The man retreats both surprised and frustrated and tries to kick me. I block him with my forearm and enter his house to try to immobilize him on the ground. I put him against a wall and dropped him flat on the floor. I grab his arms and put them behind his back to handcuff him. I get out a phone and call Darakei.</p>\n<p>Wamien: I caught him.<br>\nDarakei : Where can you come back with him without any problem?<br>\nWamien : Come get us, I still have to get his data.<br>\nMickaël : And with what password?</p>\n<p>Mickaël cut me in the middle of my sentence, smiling, he had no intention of giving it to me.</p>\n<p>Darakei: We're coming to get you.</p>"}},"chapitreAvant":"chapter-5-first-mission","chapitreApres":"chapter-7-the-datas","nomRoman":"the-first-cyborg-volume-1","slug":"chapter-6-the-encounter","codeChapitre":"H0001C006","equivalentUrl":"/histoires/chapitre/chapitre-6-la-rencontre"}},"pathContext":{"slug":"chapter-6-the-encounter"}}

